@function per_calc($size, $parent_size) {
  @return $size/$parent_size*100%; }

@mixin absolute-center {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto; }
@mixin absolute-center-bottom {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 auto; }
@mixin absolute-vertical-center {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0; }

