* {
  box-sizing: border-box; }
$base-color: #272727;
body {
  position: relative;
  min-width: 320px;
  background-color: #ffffff;
  font-family: "museo_sans_cyrl", sans-serif;
  font-weight: 500;
  margin: 0;
  color: $base-color; }
.clearfix {
  width: 100%;
  clear: both; }
.main-wrapper {
  max-width: 100%;
  overflow: hidden; }
a {
  color: #36b057;
  outline: none; }
.container {
  display: flex;
  flex-wrap: wrap;
  margin: {
    left: auto;
    right: auto; }
  @include media-md(down) {
    max-width: 100%;
    padding: 0 24px; }
  @include media-xl() {
    max-width: $xl - $margin * 2; }
  @include media-lg() {
    max-width: $lg - $margin * 2; }
  @include media-xs() {
    padding: 0 12px; } }
.container-in {
  @include grid-in(); }
input, textarea {
  outline: none;
  font-size: 16px;
  font-family: "museo_sans_cyrl", sans-serif;
  resize: none; }

.input, .textarea {
  transition: border-color .3s ease;
  &_text {
    font-size: 14px;
    color: #484848;
    padding: 10px 25px;
    border: 1px solid #c6c6c6;
    margin-top: 8px;
    width: 100%;
    &:focus, &:active {
      padding: 9px 24px;
      border: 2px solid #36b057; } }
  &.error {
    border: 1px solid red; } }

.radio {
  &_square {
    position: relative;
    text-align: center;
    & > input {
      opacity: 0; }
    & > .fake-input {
      top: 0;
      left: 0;
      position: absolute;
      width: 22px;
      height: 22px;
      border: 1px solid #c6c6c6;
      background-color: #ffffff;
      cursor: pointer;
      & > span {
        top: 3px;
        left: 3px;
        position: absolute;
        width: 14px;
        height: 14px;
        background-color: #ffffff;
        transition: background-color .3s ease; } }
    & > input:checked + span {
      & > span {
        background-color: #fcd82f; } }
    & > .label {
      font-size: 16px;
      color: #484848;
      font-weight: 300; } } }

@import "buttons";

// sprite icon toggle active state
.icon-fade {
  position: relative;
  display: inline-block;
  padding: 0;
  i {
    transition: opacity .3s ease;
    &.active {
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0; } }
  &:hover, &:focus, &.active {
    i {
      &.normal {
        opacity: 0; }
      &.active {
        opacity: 1; } } } }

.youtube-video {
  display: inline-block;
  position: relative;
  text-decoration: none;
  outline: none;
  &__play {
    position: absolute;
    padding: 20px;
    margin-top: -70/2 * 1px;
    margin-left: -68/2 * 1px;
    @include media-lg(up) {
      padding: 35px;
      margin-top: -127/2 * 1px;
      margin-left: -127/2 * 1px; }
    background-color: #292929;
    top: 50%;
    left: 50%;

    &:before {
      content: "";
      display: block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 14.5px 0 14.5px 30px;
      @include media-lg(up) {
        border-width: 30.5px 0 30.5px 52px; }
      border-color: transparent transparent transparent #ffffff;
      transform: rotate(360deg);
      transition: border-color .3s ease; }
    &:hover {
      &:before {
        border-color: transparent transparent transparent #fcd82f; } } }
  img {
    max-width: 100%;
    display: block;
    @include media-md(down) {
      width: 100%; } }
  &__title {
    position: absolute;
    color: #ffffff;
    font-size: 16px;
    &_bottom-left-black {
      bottom: 40px;
      left: 40px; }
    &_top-left-white {
      background-color: #fff;
      color: #000000;
      font-weight: 700;
      padding: 20px;
      top: 0;
      left: 0;
      @include media-md() {
        padding: 40px 30px;
        max-width: 320px; }
      @include media-sm(down) {
        position: static;
        display: block;
        background-color: #f5f5f5; } } } }


// UL styles
.ul {
  line-height: 24px;
  list-style: none;
  padding: 0;
  a {
    text-decoration: none;
    color: $base-color;
    transition: color .3s ease, font-weight .3s ease;
    & > * {
      vertical-align: middle; } }
  &_green-hover {
    a {
      color: #6a6a6a;
      &:hover {
        font-weight: 700;
        color: #36b057; } } } }

// Error 404 page
.error-404 {
  &__wrap {
    min-width: 100%;
    padding-bottom: 100px; }
  &__404 {
    font-size: 300px; }
  &__text {
    text-align: center; } }

// mixins
@mixin image-center() {
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  max-height: 100%; }

// For Vue.js
*[v-cloak] {
  display: none; }

// Cols
.col-24 {
  @include col(24); }

